// vertical superfish mod for right side
/* adding sf-vertical class in addition to sf-menu creates a vertical menu */
/* eg. <ul class="sf-menu sf-vertical"> ... */
.sf-verticalr {
  width: 150px;
  /* If you want the width of the closed menu to expand to its
  widest top-level menu item (like its "Supersubs" submenus do),
  replace the width rule above with the following two rules. */

  /*
  min-width: 12em;
  *width: 12em;
  */
}

.sf-verticalr ul {
  left: -100%; /* MY EDIT: THIS WORKS OK AT 2nd LEVEL */
  top: 0;
}

.sf-verticalr > li {
  float: none;
}

.sf-verticalr li {
  width: 100%;
}

.sf-verticalr ul ul li {
  left: -200%; /* MY EDIT: THIS WORKS OK AT 3rd LEVEL BUT SHADOW IS MISPLACED */
  top: 0;
}

/*** alter arrow directions ***/
.sf-verticalr.sf-arrows > li > .sf-with-ul:after {
  margin-top: -5px;
  margin-right: -3px;
  border-color: transparent;
  border-left-color: #dFeEFF; /* edit this to suit design (no rgba in IE8) */
  border-left-color: rgba(255, 255, 255, 0.5);
}

.sf-verticalr.sf-arrows li > .sf-with-ul:focus:after,
.sf-verticalr.sf-arrows li:hover > .sf-with-ul:after,
.sf-verticalr.sf-arrows .sfHover > .sf-with-ul:after {
  border-left-color: white;
}

// end vertical superfish mod for right side

